.float{
	position:fixed;
	width:60px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:#25d366;
	color:#FFF !important;
	border-radius:50px;
	text-align:center;
  font-size:30px;
	box-shadow: 2px 2px 3px #999;
  z-index:100;
  transition: 2s;
}
#wsp{
    color: white !important;
}

.float:hover {
	text-decoration: none;
	color: #25d366;
    background-color: #fc5185;

}

.my-float{
	margin-top:16px;
}